// angular
import { Component, ViewChild, Inject } from '@angular/core';

// app
import { routeAnimation } from '../../app.animations';
import { DialogComponent } from './../modal/dialog.component';
import { RequestMeasureComponent } from './../modal/requestmeasure.component';

import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';

@Component({
  template: require('./home.component.html'),
  styles: [require('./home.component.scss')],
  animations: [routeAnimation]
})
export class HomeComponent {

    @ViewChild('map') map : any;

    stylesData = [{
        'name': 'classic',
        'images': ['/assets/images/classic/1.jpg', '/assets/images/classic/1.jpg', '/assets/images/classic/1.jpg', '/assets/images/classic/1.jpg', '/assets/images/classic/1.jpg', '/assets/images/classic/1.jpg']
    }, {
        'name': 'modern',
        'images': ['/assets/images/classic/1.jpg', '/assets/images/classic/1.jpg']
        }];

    PROJECTS = [{
        'name': 'Alizi',
        'addres': 'flet in Kiev',
        'image': '/assets/images/classic/1.jpg',
        'attributes': ['MDF fasad', 'furniture BLUM', 'other attr']
    }, {
        'name': 'Jony',
        'addres': 'flet in Kiev',
        'image': '/assets/images/classic/1.jpg',
        'attributes': ['Wood fasad', 'furniture AMIX', 'other attr']
    }, {
        'name': 'Jony',
        'addres': 'flet in Kiev',
        'image': '/assets/images/classic/1.jpg',
        'attributes': ['Wood fasad', 'furniture AMIX', 'other attr']
    }, {
        'name': 'Jony',
        'addres': 'flet in Kiev',
        'image': '/assets/images/classic/1.jpg',
        'attributes': ['Wood fasad', 'furniture AMIX', 'other attr']
    }];


    ngOnInit() {
        var GoogleMapsLoader = require('google-maps'); // only for common js environments

        GoogleMapsLoader.KEY = 'AIzaSyBWUdvre-VFWIofy_LraV7d7BSXPqgwIC8';

        GoogleMapsLoader.load((google: any) => {
            // Create a new StyledMapType object, passing it an array of styles,
            // and the name to be displayed on the map type control.
            var styledMapType = new google.maps.StyledMapType(
                [
                    { elementType: 'geometry', stylers: [{ color: '#ebe3cd' }] },
                    { elementType: 'labels.text.fill', stylers: [{ color: '#523735' }] },
                    { elementType: 'labels.text.stroke', stylers: [{ color: '#f5f1e6' }] },
                    {
                        featureType: 'administrative',
                        elementType: 'geometry.stroke',
                        stylers: [{ color: '#c9b2a6' }]
                    },
                    {
                        featureType: 'administrative.land_parcel',
                        elementType: 'geometry.stroke',
                        stylers: [{ color: '#dcd2be' }]
                    },
                    {
                        featureType: 'administrative.land_parcel',
                        elementType: 'labels.text.fill',
                        stylers: [{ color: '#ae9e90' }]
                    },
                    {
                        featureType: 'landscape.natural',
                        elementType: 'geometry',
                        stylers: [{ color: '#dfd2ae' }]
                    },
                    {
                        featureType: 'poi',
                        elementType: 'geometry',
                        stylers: [{ color: '#dfd2ae' }]
                    },
                    {
                        featureType: 'poi',
                        elementType: 'labels.text.fill',
                        stylers: [{ color: '#93817c' }]
                    },
                    {
                        featureType: 'poi.park',
                        elementType: 'geometry.fill',
                        stylers: [{ color: '#a5b076' }]
                    },
                    {
                        featureType: 'poi.park',
                        elementType: 'labels.text.fill',
                        stylers: [{ color: '#447530' }]
                    },
                    {
                        featureType: 'road',
                        elementType: 'geometry',
                        stylers: [{ color: '#ffde00' }]
                    },
                    {
                        featureType: 'road.arterial',
                        elementType: 'geometry',
                        stylers: [{ color: '#ffde00' }]
                    },
                    {
                        featureType: 'road.highway',
                        elementType: 'geometry',
                        stylers: [{ color: '#f8ba49' }]
                    },
                    {
                        featureType: 'road.highway',
                        elementType: 'geometry.stroke',
                        stylers: [{ color: '#e9bc62' }]
                    },
                    {
                        featureType: 'road.highway.controlled_access',
                        elementType: 'geometry',
                        stylers: [{ color: '#ffde00' }]
                    },
                    {
                        featureType: 'road.highway.controlled_access',
                        elementType: 'geometry.stroke',
                        stylers: [{ color: '#db8555' }]
                    },
                    {
                        featureType: 'road.local',
                        elementType: 'labels.text.fill',
                        stylers: [{ color: '#806b63' }]
                    },
                    {
                        featureType: 'transit.line',
                        elementType: 'geometry',
                        stylers: [{ color: '#dfd2ae' }]
                    },
                    {
                        featureType: 'transit.line',
                        elementType: 'labels.text.fill',
                        stylers: [{ color: '#8f7d77' }]
                    },
                    {
                        featureType: 'transit.line',
                        elementType: 'labels.text.stroke',
                        stylers: [{ color: '#ebe3cd' }]
                    },
                    {
                        featureType: 'transit.station',
                        elementType: 'geometry',
                        stylers: [{ color: '#dfd2ae' }]
                    },
                    {
                        featureType: 'water',
                        elementType: 'geometry.fill',
                        stylers: [{ color: '#b9d3c2' }]
                    },

                    {
                        featureType: 'water',
                        elementType: 'labels.text.fill',
                        stylers: [{ color: '#92998d' }]
                    }
                ],
                { name: 'Карта МДВД' });

        // Create a map object, and include the MapTypeId to add
        // to the map type control.

            var mapProp = {
                center: new google.maps.LatLng(50.438505, 30.357338),
                zoom: 12,
                mapTypeControlOptions: {
                    mapTypeIds: ['roadmap', 'satellite', 'hybrid', 'terrain',
                        'styled_map']
                }
            };

            var map = new google.maps.Map(this.map.nativeElement, mapProp);

            //Associate the styled map with the MapTypeId and set it to display.
            map.mapTypes.set('styled_map', styledMapType);
            map.setMapTypeId('styled_map');

            // create marker
            var marker = new google.maps.Marker({
                position: new google.maps.LatLng(50.438505, 30.357338),
                map: map,
                icon: pinSymbol("#FFDE00"),
                animation: google.maps.Animation.DROP,
                title: 'Showroom MDVD!'
            });

            marker.addListener('click', animationMarker);

            function animationMarker() {
                if (marker.getAnimation() !== null) {
                    marker.setAnimation(null);
                } else {
                    marker.setAnimation(google.maps.Animation.BOUNCE);
                }
            }

            function pinSymbol(color: any) {
                return {
                    path: 'M 0,0 C -2,-20 -10,-22 -10,-30 A 10,10 0 1,1 10,-30 C 10,-22 2,-20 0,0 z M -2,-30 a 2,2 0 1,1 4,0 2,2 0 1,1 -4,0',
                    fillColor: color,
                    fillOpacity: 1,
                    strokeColor: '#000',
                    strokeWeight: 2,
                    scale: 1,
                };
            }

        });
    }

    animal: string;
    name: string;

    constructor(public dialog: MatDialog) { }

    openRequestDialog(): void {
        let dialogRef = this.dialog.open(DialogComponent, {
            width: '550px',
            data: { name: this.name, animal: this.animal }
        });

        dialogRef.afterClosed().subscribe(result => {
            console.log('The dialog was closed');
            this.animal = result;
        });

    }

    openRequestMeasure(): void {
        let dialogRef = this.dialog.open(RequestMeasureComponent, {
            width: '550px',
            data: { name: this.name, animal: this.animal }
        });

        dialogRef.afterClosed().subscribe(result => {
            console.log('The dialog was closed');
            this.animal = result;
        });

    }

}



